import {Component, OnInit} from '@angular/core';
import {fabric} from 'fabric';
import {Rect} from 'fabric/fabric-impl';

@Component({
	selector: 'map-builder',
	templateUrl: './map-builder.component.html',
	styleUrls: ['./map-builder.component.scss']
})
export class MapBuilderComponent implements OnInit {
	canvas: any;
	private wall: Rect;
	private window: Rect;
	constructor() {}

	ngOnInit(): void {
		this.canvas = new fabric.Canvas('canvas', {allowTouchScrolling: true});
		this.canvas.on('mouse:wheel', (opt) => {
			const delta = opt.e.deltaY;
			let zoom = this.canvas.getZoom();
			zoom *= 0.999 ** delta;
			if (zoom > 20) {
				zoom = 20;
			}
			if (zoom < 0.01) {
				zoom = 0.01;
			}
			this.canvas.zoomToPoint({x: opt.e.offsetX, y: opt.e.offsetY}, zoom);
			opt.e.preventDefault();
			opt.e.stopPropagation();
		});
		this.canvas.on('mouse:dblclick', (opt) => {
			opt.target.set('fill', 'red');
			opt.target.set('flipX', true);
			this.canvas.renderAll();
		});
	}

	addWp() {
		fabric.Image.fromURL('../assets/static_picture/work-place.png', (img) => {
			img.set({
				scaleX: 20 / img.width,
				scaleY: 20 / img.height
			});
			img.lockScalingX = true;
			img.lockScalingY = true;
			img.left = 80;
			img.top = 80;
			this.canvas.add(img);
		});
	}

	addWindow() {
		this.window = new fabric.Rect({
			left: 80,
			top: 80,
			width: 50,
			height: 3,
			fill: 'blue'
		});
		this.window.setControlsVisibility({
			mt: false, // middle top disable
			mb: false, // midle bottom
			bl: false,
			tl: false,
			br: false,
			tr: false
		});
		this.window.lockScalingY = true;
		this.canvas.add(this.window);
	}

	addWall() {
		this.wall = new fabric.Rect({
			left: 80,
			top: 80,
			width: 100,
			height: 3,
			fill: 'grey'
		});
		this.wall.setControlsVisibility({
			mt: false, // middle top disable
			mb: false, // midle bottom
			bl: false,
			tl: false,
			br: false,
			tr: false
		});
		this.wall.lockScalingY = true;
		this.canvas.add(this.wall);
	}
	addMR() {
		this.window = new fabric.Rect({
			left: 80,
			top: 80,
			width: 50,
			height: 50,
			stroke: 'green',
			strokeWidth: 1,
			fill: 'rgba(0,0,0,0)'
		});
		this.canvas.add(this.window);
	}

	convert() {
		alert(
			this.canvas.toSVG()
		);
	}

	addPot() {
		fabric.Image.fromURL('../assets/static_picture/pot.png', (img) => {
			img.set({
				scaleX: 20 / img.width,
				scaleY: 20 / img.height
			});
			img.setControlsVisibility({
				mt: false, // middle top disable
				mb: false, // midle bottom
				bl: false,
				tl: false,
				br: false,
				tr: false,
				mr: false,
				ml: false,
			});
			img.lockScalingX = true;
			img.lockScalingY = true;
			img.left = 80;
			img.top = 80;
			this.canvas.add(img);
		});
	}
}
