import {Injectable} from '@angular/core';
import {AdditionalFilter, Floor, SeatType} from '@pages/booking-search/entities';
import {Observable, of} from 'rxjs';
import {BookingTableItem} from '@pages/booking-search/booking-search-table/booking-table-item';
// import {BookingSearchModule} from '@pages/booking-search/booking-search.module';
import {ApiService} from '../../api.service';
import {HttpClient} from '@angular/common/http';
import {Office} from '@pages/booking-search/entities';
import {take} from 'rxjs/operators';
import {map} from 'rxjs/internal/operators';

@Injectable(
	/*{
	providedIn: BookingSearchModule
}*/
	{
		providedIn: 'root' // Explanation: I noticed a warning about circular dependency this was an easy fix
	}
)
export class BookingSearchFiltersService extends ApiService {
	private filtersJson: string;
	private changedRequest = false;
	getAvailableOffices(cityID: number): Observable<Office[]> {
		return this.get<Office[]>(`/offices/byCity/${cityID}`).pipe(take(1));
	}
	getAvailableFloors(officeID: number): Observable<Floor[]> {
		return this.get<Floor[]>(`/floors/byOffice/${officeID}`).pipe(take(1));
	}
	getAvailableSeatTypes(userID: number): Observable<SeatType[]> {
		// Is not implemented in the back
		// return this.get<SeatType[]>(`/floors/byOffice/${userID}`).pipe(take(1));
		return of([
			{
				id: 1,
				name: 'A seat in coworking'
			} /*,
			{
				id: 2,
				name: ' A conference room'
			}*/
		]);
	}
	getAvailableAdditionalFilters(/*officeID: number*/): Observable<AdditionalFilter[]> {
		/*${officeID}*/
		return this.get<AdditionalFilter[]>(`/tags`).pipe(take(1));
	}

	getBookingSearchTable(/*filters: JSON*/): Observable<BookingTableItem[]> {
		return this.post<BookingTableItem[]>(`/places/free`, this.filtersJson).pipe(
			map((data: any) => {
				return data.map((item: any) => {
					return {
						floorId: item.floorId,
						id: item.id,
						name: item.name,
						tags: item.tags
					};
				});
			})
		);
	}

	constructor(http: HttpClient) {
		super(http);
	}

	formIsGood(): boolean {
		if (this.filtersJson /*&& this.goodRequest*/) {
			return true;
		}
		return false;
	}

	sendFilters(filters: string): void {
		this.filtersJson = filters;
		this.changedRequest = true;
	}
	resetFlagChangedRequest(): void {
		this.changedRequest = false;
	}
}
