import {Injectable} from '@angular/core';
import {ApiService} from '../../api.service';
import {Observable} from 'rxjs';
import {OfficesTableItem} from '@pages/offices/entities';
import {map} from 'rxjs/internal/operators';

@Injectable()
export class OfficesService extends ApiService {
	getOffices(): Observable<OfficesTableItem[]> {
		return this.get<OfficesTableItem[]>(`/offices/extended`).pipe(
			map((data: any) => {
				return data.map((item: any) => {
					return {
						country: item.country.name,
						city: item.city.name,
						address: item.office.street,
						floors: item.floors,
						officeId: item.office.id
					};
				});
			})
		);
	}
}
