import {DataSource} from '@angular/cdk/collections';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {map} from 'rxjs/operators';
import {Observable, merge, BehaviorSubject} from 'rxjs';
import {OfficesTableItem} from '@pages/offices/entities';

export class OfficesTableDataSource extends DataSource<OfficesTableItem> {
	private $data: BehaviorSubject<OfficesTableItem[]>;

	set data(data: OfficesTableItem[]) {
		this.$data.next(data);
	}

	get data() {
		return this.$data.value;
	}

	paginator: MatPaginator;
	sort: MatSort;

	constructor() {
		super();

		this.$data = new BehaviorSubject([]);
	}

	connect(): Observable<OfficesTableItem[]> {
		const dataMutations = [this.$data, this.paginator.page, this.sort.sortChange];

		return merge(...dataMutations).pipe(
			map(() => {
				return this.getPagedData(this.getSortedData([...this.data]));
			})
		);
	}

	disconnect() {}

	private getPagedData(data: OfficesTableItem[]) {
		const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
		return data.splice(startIndex, this.paginator.pageSize);
	}

	private getSortedData(data: OfficesTableItem[]) {
		if (!this.sort.active || this.sort.direction === '') {
			return data;
		}

		return data.sort((a, b) => {
			const isAsc = this.sort.direction === 'asc';
			switch (this.sort.active) {
				case 'country':
					return compare(a.country, b.country, isAsc);
				case 'city':
					return compare(a.city, b.city, isAsc);
				case 'address':
					return compare(a.address, b.address, isAsc);
				default:
					return 0;
			}
		});
	}
}

function compare(a: string | number, b: string | number, isAsc: boolean) {
	return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
