import {AfterViewInit, Component, OnInit, ViewChild, Input, OnChanges, SimpleChanges} from '@angular/core';
import {MatDialog} from '@angular/material/dialog';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTable} from '@angular/material/table';
import {OfficesTableDataSource} from './offices-table-datasource';
import {FloorsDialogComponent} from '@pages/offices/floors-dialog/floors-dialog.component';
import {OfficesTableItem} from '../entities';

@Component({
	selector: 'app-offices-table',
	templateUrl: './offices-table.component.html',
	styleUrls: ['./offices-table.component.scss']
	// changeDetection: ChangeDetectionStrategy.OnPush
})
export class OfficesTableComponent implements AfterViewInit, OnInit, OnChanges {
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;
	@ViewChild(MatTable) table: MatTable<OfficesTableItem>;
	dataSource: OfficesTableDataSource = new OfficesTableDataSource();
	@Input() offices;

	displayedColumns = ['country', 'city', 'address', 'edit'];

	constructor(public dialog: MatDialog) {}

	ngOnInit() {}

	/*	ngOnChanges(changes: SimpleChanges) {
		if (changes.offices && this.offices) {
			this.dataSource = new OfficesTableDataSource();
			this.dataSource.data = this.offices;
			this.dataSource.sort = this.sort;
			this.dataSource.paginator = this.paginator;
			this.table.dataSource = this.dataSource;
			console.log(this.dataSource.data);
		}
	}*/

	ngOnChanges(changes: SimpleChanges) {
		if (changes.offices && this.offices) {
			this.dataSource.data = this.offices;
		}
	}

	ngAfterViewInit() {
		this.dataSource.sort = this.sort;
		this.dataSource.paginator = this.paginator;
		this.table.dataSource = this.dataSource;
	}

	openFloorsDialog(office) {
		this.dialog.open(FloorsDialogComponent, {
			data: office
		});
	}
}
