import {Component, OnInit, OnDestroy} from '@angular/core';
import {Observable} from 'rxjs';
import {OfficesService} from '@pages/offices/offices.service';
import {take} from 'rxjs/internal/operators';
import {MatDialog} from '@angular/material/dialog';
import {OfficeCreationComponent} from '@pages/office-creation/office-creation.component';

@Component({
	selector: 'offices',
	templateUrl: './offices.component.html',
	styleUrls: ['./offices.component.scss']
})
export class OfficesComponent implements OnInit, OnDestroy {
	offices$;
	subscription;

	constructor(private officesService: OfficesService, public dialog: MatDialog) {}

	ngOnInit(): void {
		this.offices$ = this.getOffices().pipe(take(1));
	}

	ngOnDestroy() {
		if (this.subscription) {
			this.subscription.unsubscribe();
			this.subscription = null;
		}
	}

	getOffices(): Observable<any[]> {
		return (this.offices$ = this.officesService.getOffices());
	}

	openOfficeCreationDlg() {
		this.subscription = this.dialog
			.open(OfficeCreationComponent)
			.afterClosed()
			.subscribe((res) => {
				console.log(res);
				this.offices$ = this.getOffices().pipe(take(1));
			});
	}
}
